<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <form>
          <!--Search Input-->
          <div>
            <div class="row">
              <div class="col-md-1">
                <fg-input>
                  <el-input
                    type="search"
                    class="mb-6"
                    clearable
                    prefix-icon="el-icon-search"
                    style="width: 200px"
                    placeholder="Search records"
                    v-model="searchQuery"
                    aria-controls="datatables"
                  ></el-input>
                </fg-input>
              </div>
            </div>
          </div>
          <!--Date Picker-->
          <div class="row">
            <label class="col-form-label" id="labelDate">DATE FROM</label>
            <div class="col-md-2">
              <fg-input>
                <el-date-picker
                  v-model="datePicker"
                  type="date"
                  placeholder="Pick a day"
                  :picker-options="pickerOptions"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </fg-input>
            </div>
            <label class="col-form-label">TO</label>
            <div class="col-md-2">
              <fg-input>
                <el-date-picker
                  v-model="datePicker1"
                  type="date"
                  placeholder="Pick a day"
                  :picker-options="pickerOptions"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </fg-input>
            </div>
          </div>
          <!--Status Radio-->
          <div class="row">
            <label class="col-md-1 col-form-label text-right" id="labelStatus">STATUS</label>
            <n-radio class="display-inline" v-model="enabledRadio" label="PENDING">PENDING</n-radio>
            <div class="col-md-1">
              <n-radio v-model="enabledRadio" label="PAID">PAID</n-radio>
            </div>
            <div class="col-md-1">
              <n-radio v-model="enabledRadio" label="ALL">ALL</n-radio>
            </div>
            <div class="col-md-1">
              <n-button
                class="btn btn-primary btn-sm"
                type="info"
                @click.native="get(enabledRadio, datePicker, datePicker1)"
              >FILTER NOW</n-button>
            </div>
          </div>
        </form>
      </card>
      <!--Cleaning Service Table-->
      <card>
        <div slot="header">
          <h5 class="title">MANAGEMENT FEES</h5>
        </div>
        <div class="col-12 d-flex align-items-end flex-column">
          <el-select
            class="select-primary p-2"
            style="width: 200px"
            v-model="pagination.perPage"
            placeholder="Per page"
          >
            <el-option
              class="select-default"
              v-for="item in pagination.perPageOptions"
              :key="item"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </div>
        <el-table :data="queriedData" @row-click="onRowClick">
          <el-table-column min-width="50" type="index" align="center"></el-table-column>
          <el-table-column min-width="150" prop="tenant" label="TENANT" align="center"></el-table-column>
          <el-table-column min-width="140" prop="blockno" label="BLOCK NO" align="center"></el-table-column>
          <el-table-column min-width="100" prop="fees" label="FEES" align="center"></el-table-column>
          <el-table-column min-width="170" prop="startDate" label="START DATE" align="center"></el-table-column>
          <el-table-column min-width="170" prop="endDate" label="EXPIRY DATE" align="center"></el-table-column>
          <el-table-column min-width="120" prop="status" label="STATUS" align="center"></el-table-column>
        </el-table>
        <div
          slot="footer"
          class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
        >
          <div class>
            <p class="card-category">Showing {{from + 1}} to {{to}} of {{total}} entries</p>
          </div>
          <n-pagination
            class="pagination-no-border"
            v-model="pagination.currentPage"
            :per-page="pagination.perPage"
            :total="total"
          ></n-pagination>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
import { DatePicker } from "element-ui";
import { Radio } from "src/components";
import { Table, TableColumn, Select, Option } from "element-ui";
import { Pagination as NPagination } from "src/components";
import Fuse from "fuse.js";

export default {
  components: {
    [DatePicker.name]: DatePicker,
    [Radio.name]: Radio,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    NPagination,
    [Select.name]: Select,
    [Option.name]: Option
  },
  computed: {
    /***
     * Returns a page from the searched data or the whole data. Search is performed in the watch section below
     */
    queriedData() {
      let result = this.management;
      if (this.searchedData.length > 0) {
        result = this.searchedData;
      }
      return result.slice(this.from, this.to);
    },
    to() {
      let highBound = this.from + this.pagination.perPage;
      if (this.total < highBound) {
        highBound = this.total;
      }
      return highBound;
    },
    from() {
      return this.pagination.perPage * (this.pagination.currentPage - 1);
    },
    total() {
      return this.searchedData.length > 0
        ? this.searchedData.length
        : this.management.length;
    }
  },
  data() {
    return {
      management: [],
      enabledRadio: "PAID",
      pickerOptions: {
        shortcuts: [
          {
            text: "Today",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Yesterday",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "A week ago",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      datePicker: "",
      datePicker1: "",
      pagination: {
        perPage: 5,
        currentPage: 1,
        perPageOptions: [5, 10, 25, 50],
        total: 0
      },
      searchQuery: "",
      propsToSearch: ["tenant"],
      searchedData: [],
      fuseSearch: null
    };
  },
  methods: {
    onRowClick(event) {
      window.location.href = `#/managementfees/editmanagementfees/${event.id}`;
    },
    //get request after press filter button
    //receive 3 parameters, enabledRadio, datePicker and datePicker1
    get(er, dp, dp1) {
      this.$http
        .get("https://admindashboard-b2ab9.firebaseio.com/management.json")
        .then(function(data) {
          return data.json();
        })
        .then(function(data) {
          //filtering by paid
          if (er == "PAID") {
            var paid_Array = [];
            var paidrange_Array = [];
            for (let key in data) {
              //if the status of data = paid
              if (data[key].status == er) {
                data[key].id = key;
                paid_Array.push(data[key]);
                //if date is null
                if (!dp || !dp1) {
                  this.management = paid_Array;
                }
                //if date is match the range (from , to)
                else if (
                  dp <= data[key].startDate &&
                  dp1 >= data[key].endDate
                ) {
                  data[key].id = key;
                  paidrange_Array.push(data[key]);
                  this.management = paidrange_Array;
                }
                //if date record not match range
                else {
                  if (paidrange_Array.length == 0) {
                    this.management = [];
                  }
                }
              }
            }
          }
          // filter by pending
          else if (er == "PENDING") {
            var pending_Array = [];
            var pendingrange_Array = [];
            for (let key in data) {
              //if the status of data = paid
              if (data[key].status == er) {
                data[key].id = key;
                pending_Array.push(data[key]);
                //if date is null
                if (!dp || !dp1) {
                  this.management = pending_Array;
                }
                //if date is match the range (from , to)
                else if (
                  dp <= data[key].startDate &&
                  dp1 >= data[key].endDate
                ) {
                  data[key].id = key;
                  pendingrange_Array.push(data[key]);
                  this.management = pendingrange_Array;
                }
                //if date record not match range
                else {
                  if (pendingrange_Array.length == 0) {
                    this.management = [];
                  }
                }
              }
            }
          }
          //filter by all
          else {
            var all_Array = [];
            var allrange_Array = [];
            for (let key in data) {
              data[key].id = key;
              all_Array.push(data[key]);
              //if date is null
              if (!dp || !dp1) {
                this.management = all_Array;
              }
              //if date is match the range (from , to)
              else if (dp <= data[key].startDate && dp1 >= data[key].endDate) {
                data[key].id = key;
                allrange_Array.push(data[key]);
                this.management = allrange_Array;
              }
              //if date record not match range
              else {
                if (allrange_Array.length == 0) {
                  this.management = [];
                }
              }
            }
          }
        });
    }
  },
  watch: {
    /**
     * Searches through the table data by a given query.
     * NOTE: If you have a lot of data, it's recommended to do the search on the Server Side and only display the results here.
     * @param value of the query
     */
    searchQuery(value) {
      let result = this.management;
      if (value !== "") {
        result = this.fuseSearch.search(this.searchQuery);
      }
      this.searchedData = result;
    }
  },
  created() {
    this.$http
      .get("https://admindashboard-b2ab9.firebaseio.com/management.json")
      .then(function(data) {
        return data.json();
      })
      .then(function(data) {
        var management_Array = [];
        for (let key in data) {
          data[key].id = key;
          //slice the start and end date to show only the date
          data[key].startDate = data[key].startDate.slice(0, 10);
          data[key].endDate = data[key].endDate.slice(0, 10);

          management_Array.push(data[key]);
        }
        this.management = management_Array;

        this.fuseSearch = new Fuse(this.management, {
          keys: ["tenant"],
          threshold: 0.3
        });
        //console.log(management_Array)
      });
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}

#labelDate {
  margin-left: 15px;
}

#labelStatus {
  margin-left: 15px;
}
</style>
